
import { defineComponent } from 'vue'
import { useI18n } from 'vue-i18n'

export default defineComponent({
  name: 'ModalDialog',
  setup() {
    const { t } = useI18n()
    return { t }
  },
  props: {
    id: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: false
    },
    centered: {
      type: Boolean,
      required: false,
      default: true
    },
    scrollable: {
      type: Boolean,
      required: false
    },
    sizeSm: {
      type: Boolean,
      required: false
    },
    sizeLg: {
      type: Boolean,
      required: false
    },
    sizeXl: {
      type: Boolean,
      required: false
    },
    closeI18nKey: {
      type: String,
      required: false,
      default: 'action.close'
    }
  }
})
